import React from 'react'
import './style.css'
import foto from './assets/photo-profile.jpeg'

export default function Inicio(){
    return(
        <div className="container-grid">
            <div className="profile-grid">
                <img className="photo" src={foto}></img>
                <div className="infos">
                    <p>Nome: David Hiromi Ono</p>
                    <p>Idade: 21 Anos</p>
                    <p>Trabalho Atual: Estagiário do InovaLab</p>
                    <p>Cidade: Presidente Prudente - SP</p>
                </div>
            </div>
            <div className="resumo">
                <div className="titulo"><h2>Sobre Mim</h2></div>               
                <p>Meu nome é David Hiromi Ono, estou no 3º Ano do curso de Ciência da Computação, na 
                    FCT-UNESP. Gosto de novos desafios para sempre me manter evoluindo e aprendendo novas 
                    tecnologias. Atualmente estou trabalhando no Laboratorio de Teste de Software, no Inova 
                    Prudente. E também sou membro da Diretoria Financeira da EJComp - Empresa Júnior da 
                    Computação na FCT-UNESP. 
                </p>
                <button>Meu Currículo</button>
            </div>
        </div>
    )
}